/* 页面框架-头部 */
.frame-header{ position: relative; height: 78px; border-bottom: 1px solid #74c3eb; background-color: #F8F8F8;}

/* 页面框架-主体 */
.frame-mainer{ position: relative; min-height: 500px; background-color: #FFF;}
.frame-mainer{ display: flex; flex-flow: row; align-items: stretch; justify-content: space-between;}
.frame-mainer .frame-l-size{ -webkit-flex:1; -moz-flex:1; flex:1; max-width: 250px;}
.frame-mainer .frame-r-size{ -webkit-flex:1; -moz-flex:1; flex:1; margin-left: 20px;}

/* 页面框架-脚部 */
.frame-footer{ background-color: #74c3eb; margin-top: 20px; text-align: center; color: #FFF;}